<template>
	<section :class="[panelClass,cname]">
		<h5>{{title}}</h5>
		<slot></slot>
	</section>
</template>

<script>
export default {

  name: 'panel',
  props: {
  	cname:{
  		type:String,
  		default: ''
  	},
  	title:{
  		type:String,
  		default: ''
  	}
  },
  data () {
    return {
    	panelClass:"panel"
    }
  }
}
</script>

<style lang="scss">
	@import "~@/css/element.scss";
	.panel{
		@include panel;
	}
</style>


